import { Facebook, Instagram, LinkedIn, LocalPhone, MailOutline, Room, Twitter, YouTube, } from "@material-ui/icons";
import styled from "styled-components";
import { mobile } from "../responsive";

const Container = styled.div`
    display:flex;
    ${mobile({ flexDirection: "column" })}
`;
const Left = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const Logo = styled.h1``;
const Desc = styled.p`
    margin: 20px 0px;
`;
const SocialContainer = styled.h1`
    display: flex;
`;
const SocialIcon = styled.h1`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: white;
    background-color: #${props=>props.color};
    display: flex;
    align-items: Center;
    justify-content: Center;
    margin-right: 20px;
`;

const Center = styled.div`
    flex: 1;
    padding: 20px;
    ${mobile({ display: "none" })}
`;
const Title = styled.h3`
    
    margin-bottom: 30px;
    
`;
const List = styled.ul`
    margin:0;
    padding:0;
    list-style: none;
    display:flex;
    flex-wrap: wrap;
`;
const ListItem = styled.li`
    width: 50%;
    margin-bottom: 10px; 
`;
const Right = styled.div`
    flex: 1;
    padding: 20px;
    ${mobile({ backgrounColor: "#fff8f8" })}
`;
const ContactItem= styled.div`
    margin-bottom: 20px;
    display: flex;
    align-items: center;
`;
const Payment = styled.img`
    width: 50%;
`;


const Footer = () => {
  return (
    <Container>
        <Left>
            <Logo>URBANTROVE</Logo>
            <Desc>There are many variations of passages of Lorem Ipsum available, but
          the majority have suffered alteration in some form, by injected
          humour, or randomised words which don’t look even slightly believable.
          </Desc>
          <SocialContainer>
            <SocialIcon color = "3B5998">
                <Facebook/>
            </SocialIcon>
            <SocialIcon color = "E4405F">
                <Instagram/>
            </SocialIcon>
            <SocialIcon color = "55ACEE">
                <Twitter/>
            </SocialIcon>
            <SocialIcon color = "3B5999">
                <LinkedIn/>
            </SocialIcon>
            <SocialIcon color = "E60023">
                <YouTube/>
            </SocialIcon>
          </SocialContainer>
        </Left>
        <Center>
            <Title>Useful Links</Title>
            <List>
                <ListItem>Home</ListItem>
                <ListItem>My Cart</ListItem>
                <ListItem>Man Fashion</ListItem>
                <ListItem>Woman Fashion</ListItem>
                <ListItem>Accessories</ListItem>
                <ListItem>My Account</ListItem>
                <ListItem>Order Tracking</ListItem>
                <ListItem>Wishlist</ListItem>
                <ListItem>About us</ListItem>
                <ListItem>Terms</ListItem>
            </List>
        </Center>
        <Right>
            <Title>
                Contact Us!
            </Title>
            <ContactItem>
                <Room style ={{marginRight: "10px"}}/>
                16 Field Ganj Ludhiana, Punjab 141008
            </ContactItem>
            <ContactItem>
                <LocalPhone style ={{marginRight: "10px"}}/>
                0161-5978342
            </ContactItem>
            <ContactItem>
            <MailOutline style ={{marginRight: "10px"}}/>
               contact@mycart.com
            </ContactItem>
            <Payment src="https://i.ibb.co/Qfvn4z6/payment.png"/>
        </Right>
    </Container>
  )
}

export default Footer;
